@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(135deg, 
    rgba(179, 229, 252, 1) 0%,
    rgba(179,229,252,1) 50%,
    rgba(240,98,146,1) 50%,
    rgba(250,98,146,1) 100%
        );
        font-family: "Montserrat", sans-serif;
        color : #78909c
}
.Jokesidebar {

    background: #9575cd;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 30%;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1), inset 0 0 25px #7e57c2;
    z-index: 100;
}

.Jokesidebar__title {
    font-size: 4rem;
    margin: 2rem;
    color : #fff;
    font-weight: 500;
    letter-spacing: 0.6rem;
}

.Jokesidebar__title > span
{
    font-weight: 700;
    letter-spacing: 0;

}

.Jokesidebar img {
    width: 50%;
    border-radius: 50%;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.Jokesidebar img:hover{
    animation:  shake-slow 5s ease-in-out 0s;
    -webkit-animation:  shake-slow 5s ease-in-out 0s;
}

.Jokesidebar__btn {
    font-size: 1.4rem;
    width: 55%;
    border: none;
    border-radius: 2rem;
    -webkit-border-radius: 2rem;
    -moz-border-radius: 2rem;
    -ms-border-radius: 2rem;
    -o-border-radius: 2rem;
    padding: 1rem 2rem;
    background: linear-gradient(135deg,     rgba(179, 229, 252, 1) 0%,    rgba(179,229,252,1) 50%,    rgba(240,98,146,1) 50%,    rgba(250,98,146,1) 100%        );
    color: #fff;
    cursor: pointer;
    font-weight: 700;
    outline: none;
    margin: 2rem;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
    word-spacing: 2.6rem;
    transition: all .2s cubic-bezier(0.2,1,0.2,1) ;
    -webkit-transition: all .2s cubic-bezier(0.2,1,0.2,1) ;
    -moz-transition: all .2s cubic-bezier(0.2,1,0.2,1) ;
    -ms-transition: all .2s cubic-bezier(0.2,1,0.2,1) ;
    -o-transition: all .2s cubic-bezier(0.2,1,0.2,1) ;
}

.Jokesidebar__btn:hover{
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.25);
    transform: translateY(-2px);
    -webkit-transform: translateY(-2px);
    -moz-transform: translateY(-2px);
    -ms-transform: translateY(-2px);
    -o-transform: translateY(-2px);
}


@-webkit-keyframes shake-slow {
