.Joke__list {
    height: 90%;
    background: #fff;
    align-self: center;
    width: 70%;
    overflow-y: scroll;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

.Joke__list::-webkit-scrollbar-track
{
    box-shadow: inset 0 0 6px rgba(0,0,0,0.3) ;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #F5F5F5;
}

.Joke__list::-webkit-scrollbar
{
	width: 12px;
	background-color: #F5F5F5;
}

.Joke__list::-webkit-scrollbar-thumb
{
    border-radius: 10px;
    box-shadow: inset 0 0 6px rgba(0,0,0,.3) ;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #9575cd;
}
.JokeItem {
    display: flex;
    border-bottom: 2px solid #eeeeee;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    padding: 1rem;
}
.JokeItem__btn
{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 15%;
    margin-right: 1rem;
}

.JokeItem__text {
    width: 75%;
    font-size: 1.2rem;  
}

.JokeItem__votes 
{
    width: 50px;
    height: 50px;
    object-fit: contain;
    line-height: 50px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    transition: border .2s ease-in;
    text-align: center;
    font-size: 20px;
    font-weight: 500;
    box-shadow: 0 10px 38px rgba(0, 0, 0,0.2), 0 10px 12px rgba(0, 0, 0,0.1);
    -webkit-transition: border .2s ease-in;
    -moz-transition: border .2s ease-in;
    -ms-transition: border .2s ease-in;
    -o-transition: border .2s ease-in;
}

.fa-arrow-up,.fa-arrow-down
{
    font-size: 1.5rem;
    margin: 10px;
    cursor: pointer;
    transition: all 0.3s;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
}

.fa-arrow-up:hover{
    color: #8bc34a;
    -webkit-animation-name: move-up;
            animation-name: move-up;
    -webkit-animation-duration: .7s;
            animation-duration: .7s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}

.fa-arrow-down:hover{
    color: #f44336;
    -webkit-animation-name: move-down;
            animation-name: move-down;
    -webkit-animation-duration: .7s;
            animation-duration: .7s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}


.JokeItem__smily
{
    font-size: 3rem;
    margin-left: auto;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    box-shadow: 0 10px 38px rgba(0, 0, 0,0.2), 0 10px 12px rgba(0, 0, 0,0.1);
}

.JokeItem__smily .em 
{
    transition: all .2s ease-in-out;
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    -ms-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
}

.JokeItem__smily:hover{
    -webkit-animation-name: spin;
            animation-name: spin;
    -webkit-animation-duration: .7s;
            animation-duration: .7s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}




@-webkit-keyframes move-up {
    0%,
    50%,
    100% {
      transform: translateY(0);
    }
    25%,
    75% {
      transform: translateY(-3px);
    }
  }




@keyframes move-up {
    0%,
    50%,
    100% {
      transform: translateY(0);
    }
    25%,
    75% {
      transform: translateY(-3px);
    }
  }

  @-webkit-keyframes move-down {
    0%,
    50%,
    100% {
      transform: translateY(0);
    }
    25%,
    75% {
      transform: translateY(3px);
    }
  }

  @keyframes move-down {
    0%,
    50%,
    100% {
      transform: translateY(0);
    }
    25%,
    75% {
      transform: translateY(3px);
    }
  }

  @-webkit-keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  30% {
    transform: rotate(20deg);
  }
  60% {
    transform: rotate(0deg);
  }
  80% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

  @keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  30% {
    transform: rotate(20deg);
  }
  60% {
    transform: rotate(0deg);
  }
  80% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
.Joke {
    display: flex;
    width: 80%;
    height: 80%;
    position: relative;
}

 .Joke .Joke__title {
    font-size: 4rem;
    margin: 2rem;
    color : #fff;
    font-weight: 500;
    letter-spacing: 0.6rem;
}

.Joke .Joke__spinner
{
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    -o-transform: translate(-50%,-50%);
    text-align: center;
}
